/**
 * Skip Navigation Link
 * Accessibility component that allows keyboard users to skip to main content
 * Hidden by default, becomes visible on focus
 *
 * WCAG 2.1 Success Criterion 2.4.1 - Bypass Blocks
 */
export function SkipLink() {
  return (
    <a
      href="#main-content"
      className="sr-only focus:not-sr-only focus:absolute focus:top-4 focus:left-4 focus:z-50 focus:px-4 focus:py-2 focus:bg-blue-600 focus:text-white focus:rounded-lg focus:font-medium focus:shadow-lg focus:outline-none"
    >
      Skip to main content
    </a>
  );
}
